.header {
  height: 80px;
  background-color: #fff;
}
.header .header-top {
  height: 40px;
  line-height: 40px;
  padding-right: 30px;
  text-align: right;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  background-color: #002140;
}
.header .header-top span {
  margin-right: 10px;
  color: #fff;
  font-size: large;
}
.header .header-bottom {
  height: 40px;
  display: flex;
  align-items: center;
}
.header .header-bottom .header-bottom-left {
  position: relative;
  width: 15%;
  text-align: center;
  font-size: 20px;
}
.header .header-bottom .header-bottom-right {
  width: 75%;
  text-align: right;
  margin-right: 30px;
}
.header .header-bottom .header-bottom-right img {
  margin: 0 15px;
  width: 30px;
  height: 20px;
}
