.add-product .ant-row {
  margin-bottom: 15px;
}
.add-product .ant-row .label {
  margin-right: 30px;
  height: 34px;
  line-height: 34px;
  text-align: right;
  color: #333;
  font-size: 14px;
  font-weight: 700;
}
.add-product .ant-row .ant-input {
  height: 34px;
}
.add-product .ant-row .select {
  width: 200px;
}
.add-product .ant-row .upload-list-inline .ant-upload-list-item {
  float: left;
  width: 200px;
  margin-right: 8px;
}
.add-product .ant-row .upload-list-inline .ant-upload-animate-enter {
  -webkit-animation-name: uploadAnimateInlineIn;
          animation-name: uploadAnimateInlineIn;
}
.add-product .ant-row .upload-list-inline .ant-upload-animate-leave {
  -webkit-animation-name: uploadAnimateInlineOut;
          animation-name: uploadAnimateInlineOut;
}
